.register {
    width : 100vw;
    height: 100vh;
    background: linear-gradient(to bottom,
            rgba(0, 0, 0, 0) 0%,
            rgba(0, 0, 0, 1) 100%),
        url(../netflix-image.jpg);
    background-size: cover;
    position: relative;

    .top {
        .wrapper {
            padding        : 20px 50px;
            display        : flex;
            align-items    : center;
            justify-content: space-between;

            .logo {
                height: 40px;
            }

            .loginButton {
                position: relative;
                z-index: 100;
                text-decoration: none;
                background-color: red;
                border          : none;
                color           : white;
                border-radius   : 5px;
                padding         : 5px 15px;
                font-size       : 16px;
                font-weight     : 500;
                cursor: pointer;
            }
        }
    }

    .myContainer {
        width          : 100%;
        height         : 100%;
        position       : absolute;
        top: 0;
        left: 0;
        // display        : flex;
        // flex-direction : column;
        // align-items    : center;
        // justify-content: center;
        color          : white;
        z-index: 10;

        h1 {
            font-size: 50px;
        }

        h2 {
            margin: 20px;
        }
        p{
            font-size: 20px;
        }
        .myInput{
            width: 50%;
            background-color: red;
            display: flex;
            align-items: center;
            justify-content: center;
            margin-top: 20px;
            height: 50px;
            border-radius: 5px;

            input{
                flex: 8;
                height: 100%;
                border: none;
                padding: 0px 10px;
            }

            .registerButton{
                flex: 4;
                height: 100%;
                background-color: red;
                border: none;
                color: white;
                font-size: 2vw;
                cursor: pointer;
            }
        }
    }
}